<template>
  <div>
    <NavbarHead></NavbarHead>
    <NavbarSide></NavbarSide>
    <div><br /><br /><br /><br /><br /></div>
    <div class="text-center p-4 m-4">
      <h2>Sub Categories of {{ category.titlec }}</h2>
    </div>
    <div class="row text-center p-4 ml-5">
      <div
        v-for="sub in subcat"
        v-bind:key="sub.idsc"
        style="background-color: grey; width: 20%"
        class="m-4"
      >
        <a class="nav-link">
          <router-link
            :to="{ path: '/ProductOfSub/' + sub.id }"
            style="text-decoration: none; color: inherit"
          >
            {{ sub.titlesc }}
          </router-link>
        </a>
      </div>
    </div>
  </div>
</template>
<script>
import NavbarHead from "../components/NavbarHead";
import NavbarSide from "../components/NavbarSide";
import Subcatservice from "../services/Subcat.service";
import Categoryservice from "../services/Category.service";

export default {
  name: "SubcatOfCat",
  data() {
    return {
      i: 0,
      datas: require("../data.json"),
      category: [],
      subcat: [],
      idofcat: "",
    };
  },
  methods: {},

  components: {
    NavbarHead,
    NavbarSide,
  },
  created() {
    this.idofcat = this.$route.params.id;
  },
  mounted: async function () {
    Subcatservice.getAllSubCategories()
      .then((res) => {
        if (res) {
          let i;
          for (i = 0; i <= res.length; i++) {
            if (res[i].catId == this.idofcat) {
              this.subcat = this.subcat.concat(res[i]);
            }
          }
        }
      })
      .catch((err) => console.log(err));

    Categoryservice.getOneCategoriesById(this.idofcat)
      .then((res) => {
        if (res) {
          this.category = res;
        }
      })
      .catch((err) => console.log(err));
  },
};
</script>