<template>
  <div class="m-4 p-1">
    <TitleFrame></TitleFrame>
    <div class="container justify-content-center mb-5">
      <div class="pt-2 mt-5" style="border: solid grey">
        <h3 class="text-center">Login {{msg}}</h3>
        <div class="text-center text-danger">{{ errormsg }}</div>
        <form id="login_form">
          <div class="row justify-content-center">
            <div>
              <div class="form-group">
                <label for="namelog">Name</label>
                <input
                  type="text"
                  class="form-control"
                  ref="namelog"
                  id="namelog"
                  name="namelog"
                  placeholder="Enter Name"
                />
              </div>
              <div class="form-group">
                <label for="passwordlog">Password</label>
                <input
                  type="passwordlog"
                  class="form-control"
                  ref="passwordlog"
                  id="passwordlog"
                  name="passwordlog"
                  placeholder="Password"
                />
              </div>
            </div>
            <div>
              <button
                type="button"
                v-on:click="login()"
                class="btn btn-primary ml-4"
                style="margin-top: 60%"
              >
                Login
              </button>
            </div>
          </div>
        </form>
      </div>
      <div class="mt-5 pt-2" id="formulaire" style="border: solid grey">
        <h3 class="text-center">Signup</h3>
        <form id="signin_form">
          <div class="row justify-content-center">
            <div>
              <div class="form-group">
                <label for="name">Name</label>
                <input
                  type="text"
                  class="form-control"
                  ref="name"
                  id="name"
                  name="name"
                  placeholder="Enter Name"
                />
              </div>
              <div class="form-group">
                <label for="password">Password</label>
                <input
                  type="password"
                  class="form-control"
                  ref="password"
                  id="password"
                  name="password"
                  placeholder="Password"
                />
              </div>
            </div>
            <div>
              <button
                type="button"
                v-on:click="signup()"
                class="btn btn-primary ml-4"
                style="margin-top: 60%"
              >
                Signup
              </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
import TitleFrame from "../components/TitleFrame";
import axios from "axios";
import cryptoJS from "crypto-js";

export default {
  name: "LoginPage",
  components: {
    TitleFrame,
  },
  data: function () {
    return {
      errormsg: "",
      msg:"",
    };
  },
  methods: {
    login() {
      var self = this;
      let mdp = cryptoJS.SHA1(this.$refs.passwordlog.value);
      axios
        .post("https://apiexpressjs4category.herokuapp.com/api/auth/signin", {
          username: this.$refs.namelog.value,
          password: mdp,
        })
        .then(function (response) {
          console.log(response);
          localStorage.setItem("jwt", response.data.accessToken);
          localStorage.setItem("name", response.data.username);
          window.location = "/ArmaHome";
        })
        .catch(function (error) {
          console.log(error);
          self.error = "login or password incorrect.";
        });
    },
    signup() {
      var self = this;
      axios
        .post("https://apiexpressjs4category.herokuapp.com/api/auth/signup", {
          username: this.$refs.name.value,
          password: this.$refs.password.value,
          email: Math.random().toString(2).substr(2, 15),
          roles: ["user"],
        })
        .then(function (response) {
          console.log(response)
          localStorage.setItem("jwt", response.data.accessToken);
          localStorage.setItem("name", response.data.username);
          document.getElementById("formulaire").classList.add('hidden')
          self.msg = "- Connectez Vous !"
        })
        .catch(function (error) {
          console.log(error);
          self.error = "signup error";
        });
    },
  },
};
</script>
<style>
  .hidden {
    display: none;
  }
</style>