<template>
  <div>
    <NavbarHead></NavbarHead>
    <NavbarSide></NavbarSide>
    <div><br /><br /><br /><br /><br /></div>
    <div class="text-center p-4 m-4">
      <h2>All Sub Categories</h2>
    </div>

    <div class="row text-center p-4 ml-5">
      <div
        v-for="sub in subcat"
        v-bind:key="sub.idsc + sub.titlesc"
        style="background-color: grey; width: 20%"
        class="m-4"
      >
        <a class="nav-link">
          <router-link
            :to="{ path: '/ProductOfSub/' + sub.id }"
            style="text-decoration: none; color: inherit"
          >
            {{ sub.titlesc }}
          </router-link>
        </a>
      </div>
    </div>
  </div>
</template>
<script>
import NavbarHead from "../components/NavbarHead";
import NavbarSide from "../components/NavbarSide";
import Subcatservice from "../services/Subcat.service";

export default {
  name: "SubCategories",
  data() {
    return {
      i: 0,
      category: {},
      subcat: [],
    };
  },
  methods: {},

  components: {
    NavbarHead,
    NavbarSide,
  },
  created() {},
  mounted: async function () {
    Subcatservice.getAllSubCategories()
      .then((res) => {
        if (res) {
          this.subcat = res;
        }
      })
      .catch((err) => console.log(err));
  },
};
</script>