<template>
  <div>
    <NavbarHead></NavbarHead>
    <NavbarSide></NavbarSide>
    <div><br /><br /><br /><br /><br /></div>
    <div class="text-center p-4 m-4">
      <h2>Product Sheet of {{ oneproduct.name }}</h2>
      <div class="row container lg-col-12 mt-4">
        <div class="col-6">
          <div style="background-color: grey">IMAGE</div>
          <div>
            <button
              type="button"
              v-on:click="buy()"
              class="btn btn-primary"
              style="background-color: grey; border: grey"
            >BUY
            </button>
          </div>
        </div>
        <div class="col-6">
          <div>
            <h4>Name : {{ oneproduct.name }}</h4>
          </div>
          <div>
            <h4>Model : {{ oneproduct.description }}</h4>
          </div>
          <div>
            <h4>Power : {{ oneproduct.power }}</h4>
          </div>
          <div>
            <h4>Price : {{ oneproduct.price }}</h4>
          </div>
        </div>
      </div>
    </div>
    <div class="row text-center p-4 ml-5">
      <div style="background-color: grey; width: 20%" class="m-4"></div>
    </div>
  </div>
</template>
<script>
import NavbarHead from "../components/NavbarHead";
import NavbarSide from "../components/NavbarSide";
import Productservice from "../services/Product.service";

export default {
  name: "Category",
  data() {
    return {
      oneproduct: [],
    };
  },

  components: {
    NavbarHead,
    NavbarSide,
  },

  methods: {
      buy(){
          let i
          
          if( parseInt(localStorage.getItem("tmp")) == 0){
              console.log("buy1")
            localStorage.removeItem("tmp")
            i = 1
            localStorage.setItem("tmp",i)
            localStorage.setItem("article"+i,JSON.stringify(this.oneproduct.id))
          }else{  
              console.log("buy2")
              i = parseInt(localStorage.getItem("tmp"))+1
              
              localStorage.removeItem("tmp")
              localStorage.setItem("tmp",i)
              localStorage.setItem("article"+i,JSON.stringify(this.oneproduct.id))
          }
      }
  },

  created() {
      localStorage.setItem("tmp",0)
      this.idproduct = this.$route.params.id;
  },
  mounted: async function () {
    Productservice.getOneProductById(this.idproduct)
      .then((res) => {
        if (res) { 
          this.oneproduct = res;
        }
      })
      .catch((err) => console.log(err));
  },
};
</script>