<template>
  <div>
    <NavbarHead></NavbarHead>
    <NavbarSide></NavbarSide>
    <div><br /><br /><br /><br /><br /></div>
    <div class="text-center p-4 m-4">
      <h2>Products of {{ onesubcat.titlesc }}</h2>
    </div>

    <div class="row text-center p-4 ml-5">
      <div
        v-for="prod in prods"
        v-bind:key="prod.name + prod.id"
        style="background-color: grey; width: 20%"
        class="m-4"
      >
        <a class="nav-link">
          <router-link
            :to="{ path: '/OneProd/' + prod.id }"
            style="text-decoration: none; color: inherit"
          >
            {{ prod.name }}
          </router-link>
        </a>
      </div>
    </div>
  </div>
</template>
<script>
import NavbarHead from "../components/NavbarHead";
import NavbarSide from "../components/NavbarSide";
import Productservice from "../services/Product.service";
import Subcatservice from "../services/Subcat.service";

export default {
  name: "ProductOfSub",
  data() {
    return {
      prods: [],
      idsubcat: "",
      onesubcat: [],
    };
  },
  methods: {},

  components: {
    NavbarHead,
    NavbarSide,
  },
  created() {
    this.idsubcat = this.$route.params.id;
  },
  mounted: async function () {
    Subcatservice.getOneSubCategoriesById(this.idsubcat)
      .then((res) => {
        if (res) {
          this.onesubcat = res;
        }
      })
      .catch((err) => console.log(err));

    Productservice.getAllProduct()
      .then((res) => {
        if (res) {
          let i;
          for (i = 0; i <= res.length; i++) {
            if (res[i].subcatId == this.idsubcat) {
              this.prods = this.prods.concat(res[i]);
            }
          }
        }
      })
      .catch((err) => console.log(err));
  },
};
</script>